import {Component, OnInit, ViewChild} from '@angular/core';
import {AssemblyMan, Period} from '../../../interfaces/model';
import {ModalAsambleistaPage} from '../../modal/modal-asambleista/modal-asambleista.page';
import {IonInfiniteScroll, ModalController} from '@ionic/angular';
import {RestService} from '../../../service/rest.service';

@Component({
  selector: 'app-tab-alternos',
  templateUrl: './tab-alternos.page.html',
  styleUrls: ['./tab-alternos.page.scss'],
})
export class TabAlternosPage implements OnInit {
    assemblyMan: AssemblyMan[] = [];
    searchAlterno = '';
    photoMan: any;
    periodActive: Period =  new Period();
    allPeriod: Period[] = [];
    arrayPeriod: Period[] = [];
    i = 0;
    // @ts-ignore
    @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;
    constructor(public modalController: ModalController,
                private rest: RestService) { }

  ngOnInit() {
    this.getPeriod();
    this.getPeriodAll();
  }
    getPeriod() {
        this.rest.getPeriod().subscribe(respo => {
                this.periodActive =  respo;
            }, error1 => {}, () => {
                this.listAssemblyByPeriod();
            }
        );
    }
    getPeriodAll() {
        let i = 0;
        this.rest.getPeriodAll().subscribe(response => {
                this.allPeriod = response;
                this.allPeriod.forEach(data => {
                    if (!data.name.indexOf('ASAMBLEA NACIONAL')) {
                        this.arrayPeriod[i] = data;
                        i = i + 1;
                    }
                });
            }, error1 => {}, () => {

            }
        );
    }
    onChange($event) {
        this.periodActive = $event.target.value;
        this.assemblyMan = [];
        this.listAssemblyByPeriod();
    }

    public loadData(event) {
        setTimeout(() => {
            this.listAssemblyByPeriod(event);
        }, 500);
    }
    listAssemblyByPeriod(event?) {
        this.infiniteScroll.disabled = false;
        const incre = 30;
        if (event !== undefined) {
            this.i = this.i + incre;
        }
        this.rest.getAssemblyMembersResource({
            periodId: this.periodActive.id,
            territorialId: 0,
            politicalPartyId: 0,
            includePicture: false,
            onlyActives: false,
            onlyAlterns: true,
            assembly: 0,
            offset: this.i,
            limit: 30
        }).subscribe( response => {
            this.assemblyMan.push(...response);
            if (this.assemblyMan.length === 0) {
                event.target.disabled = true;
                return;
            }
            if (event !== undefined) {
                event.target.complete();
            }
        }, () => {
            console.log('Ha ocurrido un error');
        }, () => {
            if (this.assemblyMan) {
                this.listAssemblyPhoto();
            }
        });
    }

    listAssemblyPhoto() {
        this.assemblyMan.forEach((data) => {
            this.getPhoto(data);
        });
    }

    getPhoto(data: AssemblyMan) {
        this.rest.getAssemblymanPhoto({
            assemblyMemberId: data.id
        }).subscribe( responsePhoto => {
              const blob = new Blob([responsePhoto], {
                  type: 'image/png'
              });
          if (blob.size > 0) {
              const reader = new FileReader();
              reader.readAsDataURL(blob);
              reader.onloadend = () => {
                  this.photoMan = reader.result;
                  data['imageChange'] = this.photoMan;
              };
          } else {
              data['imageChange'] = 0;
          }
        }
        );
    }

    async changeModal(id, image) {
        const modal = await this.modalController.create({
            component: ModalAsambleistaPage,
            componentProps: {
                'periodActive': this.periodActive.id,
                'idAssembly': id,
                'imageChange': image,
                'assembly': this.assemblyMan.filter(data => data.id === id)
            }
        });
        return await modal.present();
    }

    searchItems(event) {
        const f = event.target.value;
        if ( f && f.toString().length > 2 ) {
            this.rest.getAssemblyMembersResource({
                periodId: this.periodActive.id,
                territorialId: 0,
                politicalPartyId: 0,
                includePicture: false,
                onlyActives: false,
                onlyAlterns: true,
                assembly: f,
                offset: 0,
                limit: 30
            }).subscribe( response => {
                this.assemblyMan = response;
            }, () => {
            }, () => {
                if (this.assemblyMan) {
                    this.listAssemblyPhoto();
                    this.infiniteScroll.disabled = true;
                }
            });
        } else if (this.searchAlterno === '') {
            this.assemblyMan = [];
            this.listAssemblyByPeriod();
        }
    }
    getClear() {
        this.i = 0;
        this.assemblyMan = [];
        this.listAssemblyByPeriod();
    }
}
